<template>
  <main class="content container" v-if="productLoading">
     ...
  </main>
  <main class="content container" v-else-if="!productData">
       
  </main>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{ name: 'main' }">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> {{ product.title }} </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img
            width="570"
            height="570"
            :src="product.image.file.url"
            :alt="product.title"
          />
        </div>
      </div>

      <div class="item__info">
        <span class="item__code">: {{ product.id }}</span>
        <h2 class="item__title">{{ product.title }}</h2>
        <div class="item__form">
          <form
            class="form"
            action="#"
            method="POST"
            @submit.prevent="addToCart"
          >
            <b class="item__price"> {{ product.price | numberFormat }}  </b>
            <fieldset class="form__block">
              <legend class="form__legend">:</legend>
              <ProductColors
                :arrayOfColors="product.colors"
                :currentColor.sync="currentColor"
              />
            </fieldset>

            <div class="item__row">
              <BaseInputSpinner class="form__counter" :amount.sync="amount" />

              <button
                class="button button--primery"
                type="submit"
                :disabled="productAddSending"
              >
                 
              </button>
            </div>

            <div class="item__alert" v-show="productAdded">
                 
            </div>
            <div class="item__alert" v-show="productAddSending">
                 ...
            </div>
          </form>
        </div>
      </div>

      <div class="item__desc">
        <ul class="tabs">
          <li class="tabs__item">
            <a class="tabs__link tabs__link--current">  </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">  </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">  </a>
          </li>
          <li class="tabs__item">
            <a class="tabs__link" href="#">    </a>
          </li>
        </ul>

        <div class="item__content">
          <p>
             GPS, , BEIDOU Galileo  QZSS<br />
              <br />
              Bluetooth Smart, ANT+  Wi-Fi<br />
              <br />
          </p>

          <a href="#">   </a>

          <h3> ?</h3>

          <p>
            Wahoo ELEMNT BOLT GPS   ,  
              ,   
            . Wahoo ELEMNT BOLT GPS   
            ANT+,     .  
             ,     .    
                 ,  
               .  
             ,     
            .      
               .
          </p>

          <h3></h3>

          <p>
             Wahoo ELEMNT BOLT  .  
              74,6 x 47,3 x 22,1 .    
            .      .
                  
            56 .      ,  
                 :
            ,   ,   .. ( 
               ).    
               IPX7. ,     , 
               ( 30 )    
               1.
          </p>
        </div>
      </div>
    </section>
  </main>
</template>

<script>
import axios from "axios";
import { mapActions } from "vuex";

import BaseInputSpinner from "@/components/BaseInputSpinner";
import ProductColors from "@/components/ProductColors";
import { API_BASE_URL } from "@/config";
import numberFormat from "@/helpers/numberFormat";

export default {
  name: "ProductPage",
  data() {
    return {
      productAmount: 1,
      productId: this.$route.params.id,

      productData: null,

      productLoading: false,
      productLoadingFailed: false,

      productAdded: false,
      productAddSending: false,

      currentColor: 0,
    };
  },
  components: {
    BaseInputSpinner,
    ProductColors,
  },
  methods: {
    ...mapActions(["addProductToCart"]),
    loadProduct() {
      this.productLoading = true;
      this.productLoading = false;

      axios
        .get(`${API_BASE_URL}/api/products/${this.$route.params.id}`)
        .then((response) => (this.productData = response.data))
        .catch(() => (this.productLoadingFailed = true))
        .then(() => (this.productLoading = false));
    },
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;
      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
      }).then(() => {
        this.productAdded = true;
        this.productAddSending = false;
      });
    },
  },
  filters: {
    numberFormat,
  },
  computed: {
    amount: {
      get() {
        return this.productAmount;
      },

      set(value) {
        if (value >= 0) {
          this.productAmount = value;
        }
      },
    },
    product() {
      return this.productData && this.productData;
    },
    category() {
      return this.productData && this.productData.category;
    },
  },
  watch: {
    "$route.params.id": {
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
};
</script>

<style>
.item__alert {
  font-size: 18px;
  margin-top: 20px;
}
</style>
